.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.chat-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 95vh;
  padding: 10px 20px;
  font-size: 22px;
  /* align-items: flex-end; */
}

.show-chat {
  display: flex;
  width: 100%;
  min-height: calc(70vh - 10vh);
}

.user-info {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px 0;
  width: calc(100% - 80%);
  flex-grow: 1;
}

.user {
  padding: 10px 0;
}

.chat-messages {
  display: flex;
  overflow-y: auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  width: calc(100% - 20%);
  flex-direction: column;
  flex-grow: 1;
}

.message {
  padding: 8px 12px;
  border-radius: 10px;
  word-wrap: break-word;
}

.message.me {
  align-self: flex-end;
  background-color: #007bff;
  color: #fff;
}

.message.you {
  align-self: flex-start;
  background-color: #e6e6e6;
}

.chat-input {
  display: flex;
  align-items: center;
  width: 100%;
}

input[type="text"] {
  width: calc(100% - 70px);
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
  margin-left: 10px;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
